[class*="col-"] {
  float: left;
}

.card-general {
  background-color: rgba(255, 255, 255, 0.7);
  margin: 10px;
  padding: 20px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

body {
  background-image: url("mountains.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  max-width: 800px;
  margin: auto;
}

/* For mobile phones: */
[class*="col-"] {
  width: 100%;
}

/* For tablets (+ desktop): */
@media only screen and (min-width: 600px) {
  .col-1 {
    width: 8.33%;
  }
  .col-2 {
    width: 16.66%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.33%;
  }
  .col-5 {
    width: 41.66%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.33%;
  }
  .col-8 {
    width: 66.66%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33%;
  }
  .col-11 {
    width: 91.66%;
  }
  .col-12 {
    width: 100%;
  }
  .card-general {
    margin: 10px 0;
  }
}

/* For desktop: */
@media only screen and (min-width: 768px) {
}
